.App {
  text-align: center;
  background-color:lightblue;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.filter-list {
  margin-top: 20px;
  width: 100%; 
  max-width: 400px; 
  margin-left:39%;
}

.filter-list h1 {
  font-size: 24px;
  margin-bottom: 10px;
}

.filter-list input {
  padding: 8px;
  margin-bottom: 10px;
  width: 100%;
}


